import 'ol/ol.css';
import 'bootstrap/dist/css/bootstrap.css';
import '@fortawesome/fontawesome-free/css/all.css';
import './css/main.css';

import $ from 'jquery';
import 'bootstrap';

import {fromLonLat} from 'ol/proj.js';
import Projection from 'ol/proj/Projection.js';
import {register} from 'ol/proj/proj4.js';
import proj4 from 'proj4';


import {Map, View} from 'ol';
import {Polygon, MultiPolygon} from 'ol/geom';
import TileLayer from 'ol/layer/Tile';
import {
  equalTo as equalToFilter,
  like as likeFilter,
  and as andFilter
} from 'ol/format/filter.js';
import {WFS, WKT, GeoJSON} from 'ol/format.js';
import {Vector as VectorLayer} from 'ol/layer.js';
import VectorSource from 'ol/source/Vector.js';
import {Stroke, Style} from 'ol/style.js';
import TileWMS from 'ol/source/TileWMS.js';

var BreakException = {};

window.app = {};
var app = window.app;

proj4.defs("EPSG:4326","+proj=longlat +datum=WGS84 +no_defs");
proj4.defs("EPSG:32633","+proj=utm +zone=33 +datum=WGS84 +units=m +no_defs");
proj4.defs("EPSG:3035","+proj=laea +lat_0=52 +lon_0=10 +x_0=4321000 +y_0=3210000 +ellps=GRS80 +towgs84=0,0,0,0,0,0,0 +units=m +no_defs");

var projection32633 = new Projection({
  code: 'EPSG:32633'
});
var projection3035 = new Projection({
  code: 'EPSG:3035'
});
register(proj4);

var vectorSource = new VectorSource({
  featureProjection:"EPSG:4326"
});
var vector = new VectorLayer({
  name: 'Selected features',
  source: vectorSource,
  style: new Style({
    stroke: new Stroke({
      color: 'rgba(0, 0, 255, 1.0)',
      width: 2
    })
  })
});

var gadmLevel0Source = new TileWMS({
  url: 'http://173.212.238.231:8080/geoserver/gadm/wms',
  params: {'LAYERS': 'gadm:gadm_level0', 'TILED': true},
  projection: 'EPSG:4326',
  serverType: 'geoserver',
  crossOrigin: 'anonymous'
});

var gadmLevel1Source = new TileWMS({
  url: 'http://173.212.238.231:8080/geoserver/gadm/wms',
  params: {'LAYERS': 'gadm:gadm_level1', 'TILED': true},
  projection: 'EPSG:4326',
  serverType: 'geoserver',
  crossOrigin: 'anonymous'
});

var gadmLevel2Source = new TileWMS({
  url: 'http://173.212.238.231:8080/geoserver/gadm/wms',
  params: {'LAYERS': 'gadm:gadm_level2', 'TILED': true},
  projection: 'EPSG:4326',
  serverType: 'geoserver',
  crossOrigin: 'anonymous'
});

var gadmLevel0 = new TileLayer({
  source: gadmLevel0Source,
  name: "GADM - Countries"
});

var gadmLevel1 = new TileLayer({
  source: gadmLevel1Source,
  name: "GADM - Regions",
  visible: false
});

var gadmLevel2 = new TileLayer({
  source: gadmLevel2Source,
  name: "GADM - Municipalities",
  visible: false
});

var map = new Map({
  target: 'map',
  layers: [
    gadmLevel0,
    gadmLevel1,
    gadmLevel2,
    vector
  ],
  view: new View({
    projection: 'EPSG:4326',
    center: [19,48],
    zoom: 8
  })
});

/** Get polygon via WFS --> it is not used */
function getPolygon(featureType, filterAtt, filterVal) {
  var featureRequest = new WFS().writeGetFeature({
    srsName: 'EPSG:4326',
    featureNS: 'operandum-gadm',
    featurePrefix: 'gadm',
    featureTypes: [featureType],
    outputFormat: 'application/json',
    filter: equalToFilter(filterAtt, filterVal)
  });
  
  //then post the request and add the received features to a layer
  fetch('http://173.212.238.231:8080/geoserver/gadm/wfs', {
  method: 'POST',
  body: new XMLSerializer().serializeToString(featureRequest)
  }).then(function(response) {
  return response.json();
  }).then(function(json) {
  var features = new GeoJSON().readFeatures(json);
  vectorSource.clear();
  vectorSource.addFeatures(features);
  map.getView().fit(vectorSource.getExtent());
  });
}


/** Create layer list */
var layerList = document.getElementById('layer-list');
map.getLayers().forEach(function(layer) {
  var layerName = layer.get('name');
  //console.log(layer);
  var layerItemLi = document.createElement('li');
  var layerItemA = document.createElement('a');
  var layerItemI = document.createElement('i');
  layerItemI.setAttribute('class', 'fa fa-check');
  layerItemA.setAttribute('href', '#');
  if (layer.get('visible')) {
    layerItemA.setAttribute('class', 'layer-on');
    layerItemI.setAttribute('class', 'fa fa-check-double');
  }
  layerItemA.setAttribute('id', layerName);
  var layerNameText = document.createTextNode(' ' + layerName);
  layerItemA.appendChild(layerItemI);
  layerItemA.appendChild(layerNameText);
  layerItemLi.appendChild(layerItemA);
  layerList.appendChild(layerItemLi);
});


